@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #1a1a1c;
  /* background-color: #3f5efb; */
  /* background-color: #191970; */
  /* font-size: 62.5%; */
  /* color: #fff; */
}

.coin-app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 40px;
}
.coin-text {
  margin-bottom: 32px;
  text-align: center;
  color: black;
}

.coin-input {
  outline: none;
  padding-left: 16px;

  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  /* background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  ); */
  background: linear-gradient(to right, #fc466b, #3f5efb);

  color: #e2e2e2;
  /* color: black; */
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
  /* color: black; */
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
  /* color: black; */
}

.coin-input::placeholder {
  color: #e2e2e2;
  /* color: black; */
}

.developer {
  position: absolute;
  top: 10px;
  /* top: 1rem; */
  text-align: center;
}

